.popular-movies
{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-column-gap:1rem;
  grid-row-gap:2rem;
}

img{
  width: 70%;
  height: 30vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom: 1rem;
  align-items: center;
}

h2
{
  font-size: 1rem;
}

.filter-button
{
  margin-right:1vw;
  margin-bottom: 2vh;
 
}

.active{
  background-color: black !important;
  color: white !important;
}